
	// Variables

	// color
	$color-base-bg: #fff;
	$color-base-text: #000;
	$color-header-bg: #5a008c;
	$color-article-bg: #eddef5;
	$color-list-bg: var(--color-article-bg);
	$color-list-test: rgba(0, 0, 0, 0.7);
	$color-modal-bg: #23d7c3;
	$color-btn-bg: #ff5f55;
	$color-btn-bg-hover: #e64d55;
	$color-btn-text: var(--color-article-bg);

	// Breakpoints
	$breakpoint-large: 1201px;
	$breakpoint-medium: 901px;
	$breakpoint-small: 751px;

	// Animation
	$box-shadow: 0 0 25px rgba(0,0,0, .2);
	$transform: translateY(0) scale(1);
	$box-shadow-hover: 0 0 35px rgba(0, 0, 0, 0.4);
	$transform-hover: translateY(-0.2rem) scale(1.01);
