/* BASE */
html {
	font-size: 62.5%
}

body {
	font-family: 'Vollkorn', serif;
	font-size: 18px;
	line-height: 1.5;
	color: $color-base-text;
	text-rendering: optimizeLegibility;
	background-color: $color-base-bg;
}

html, body {
	overflow-x: hidden;
}

header, footer, nav {
	display: flex;
	flex-flow: wrap;
}

ul {
	list-style: none;
}

label {
	display: block;
	margin-bottom: .5rem;
}
select, input {
	display: block;
	margin-bottom: 1rem;
	height: 4rem;
	font-family: 'Fredoka One', cursive;
	font-size: 1.5rem;
	line-height: 3rem;
}
