        .card__form__select {
          width: 20rem;
          min-width: 20rem; } }
    .card__form__result--option, .card__form__result--message {
      display: none; }
    .card__form--button {
      margin-bottom: 1rem; }
      .card__form--button .btn {
        margin-top: 1.5rem;
        margin-right: 1.5rem; }
        .card__form--button .btn.is-active {
