/* COMPONENT TABLE */
.table {
  margin: 3rem auto;
  width: 90%; }
  .table__caption {
    margin-bottom: 1rem;
    width: 100%;
    font-family: 'Fredoka One', cursive;
    font-size: 2.5rem; }
  .table__head--title, .table__head--content, .table__body--title, .table__body--content {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    border-left: solid 0.1rem #5a008c; }
    .table__head--title th,
    .table__head--title td, .table__head--content th,
    .table__head--content td, .table__body--title th,
    .table__body--title td, .table__body--content th,
    .table__body--content td {
      padding: 1.3rem;
      width: calc(100% / 5);
      text-align: center;
      border-right: solid 0.1rem #5a008c;
      border-bottom: solid 0.1rem #5a008c; }
  .table__head--title, .table__body--title {
    font-family: 'Fredoka One', cursive;
    color: #fff;
    background-color: #ff5f55;
    border-top: solid 0.1rem #5a008c; }

/* COMPONENT MODAL */
.modal {
  margin: 2rem auto;
  width: 90%;
  padding: 3rem;
  background-color: #23d7c3;
  box-shadow: 0 0 35px rgba(0, 0, 0, 0.4); }
  @media screen and (min-width: 901px) {
    .modal {
      box-shadow: 0 0 25px rgba(0, 0, 0, 0.2);
      transform: translateY(0) scale(1);
      transition: all 0.25s ease-in-out; }
      .modal:hover {
        box-shadow: 0 0 35px rgba(0, 0, 0, 0.4);
        transform: translateY(-0.2rem) scale(1.01); } }
  @media screen and (min-width: 1201px) {
    .modal {
      width: 60%; } }
  .modal__title {
    margin-bottom: 3rem;
    text-align: center; }
  .modal p {
    margin: 0 2rem 1rem;
    font-size: 2rem;
    line-height: 1.2; }
  .modal__button {
    margin: 6rem 0 2rem; }

/* COMPONENTS BUTTON */
.btn {
  padding: 1.5rem 4rem;
  width: 100%;
  font-family: 'Fredoka One', cursive;
  font-size: 2rem;
  color: #fff;
  background-color: #ff5f55;
  border: 0.1rem solid #ff5f55;
  cursor: pointer;
  transform: translateY(0) scale(1);
  transition: all 0.25s ease-in-out; }
  .btn:hover {
    border: 0.1rem outset #ff5f55;
    transform: translateY(-0.2rem) scale(1.01); }
  @media screen and (min-width: 751px) {
    .btn {
      width: 20rem;
      min-width: 20rem; } }
  .btn:hover {
    background-color: #e64d55; }
